import React from 'react';
import styles from './App.module.css';

class TodoListTask extends React.Component {

    state = {
        isEditMode: false,
    }

    activatedEditMode = () => {
        this.setState({isEditMode: true})
    }
    deactivatedEditMode = () => {
        this.setState({isEditMode: false})
    }

    onIsDoneChanged = (e) => {
        this.props.changeStatus(this.props.task.id, e.currentTarget.checked);
    }

    onTitleChanged = (e) => {
        this.props.changeTitle(this.props.task.id, e.currentTarget.value);
    }

    render = () => {

        let containerCssClass = this.props.task.isDone ? styles.todoListTaskDone : styles.todoListTask;

        return (
            <div className={containerCssClass}>
                <input type="checkbox" checked={this.props.task.isDone}
                       onChange={this.onIsDoneChanged}/>
                {this.state.isEditMode ? <input value={this.props.task.title}
                                                autoFocus={true}
                                                onBlur={this.deactivatedEditMode}
                                                onChange={this.onTitleChanged}/> : <span
                    onDoubleClick={this.activatedEditMode}>{this.props.task.id}: {this.props.task.title}</span>} -
                <span> priority: {this.props.task.priority}</span>
            </div>
        );
    }
}

export default TodoListTask;

